<template>
  <div id="x_industry_dynamic_text">
    <div class="out_line">
      <div class="hot_search">
        <x_hotsearch></x_hotsearch>
      </div>

      <!--左边长列表-->
      <div class="box1-left">
        <div class="box2-left">
          <x_currentlocation v-bind:current_where="current_place2" v-bind:last_location="last_location" v-bind:last_last_location="last_last_location"></x_currentlocation>
        </div>
        <div class="box8-left">
          <Title v-bind:time="time" v-bind:reading_amount="reading_amount" v-bind:title="title" v-bind:source="source"></Title>
        </div>
        <div class="box3-left">
          <x_Text v-bind:content="content" v-if="JSON.parse(this.$route.query.item).newsUrl != '/information/findReportInfo'"></x_Text>
<!--           <el-collapse  v-if="JSON.parse(this.$route.query.item).newsUrl == '/information/findReportInfo'" style="margin-top:200px;backgraound:rgba(255, 255, 255, 0.95);">
            <el-collapse-item title="内容" name="1">
              <div v-html="this.getCount.information.content"></div>
            </el-collapse-item>
            <el-collapse-item title="传递类型" name="2">
              <div>{{this.getCount.information.delivery}}</div>
            </el-collapse-item>
            <el-collapse-item title="第一次提交时间" name="3">
              <div>{{this.getCount.information.firstPostDate}}</div>
            </el-collapse-item>
            <el-collapse-item title="图像个数" name="4">
              <div>{{this.getCount.information.graphNum}}</div>
            </el-collapse-item>
            <el-collapse-item title="导读" name="5">
              <div>{{this.getCount.information.guide}}</div>
            </el-collapse-item>
            <el-collapse-item title="所属专业领域" name="6">
              <div>{{this.getCount.information.industry}}</div>
            </el-collapse-item>
            <el-collapse-item title="最近更新时间" name="7">
              <div>{{this.getCount.information.postDate}}</div>
            </el-collapse-item>
            <el-collapse-item title="创作所属公司" name="8">
              <div>{{this.getCount.information.production}}</div>
            </el-collapse-item>
            <el-collapse-item title="报告字数" name="9">
              <div>{{this.getCount.information.reportNum}}</div>
            </el-collapse-item>
            <el-collapse-item title="报告页数" name="10">
              <div>{{this.getCount.information.reportPage}}</div>
            </el-collapse-item>
            <el-collapse-item title="图片展示" name="11">
              <img :src="this.getCount.information.img">
            </el-collapse-item>
            <el-collapse-item title="关键词" name="12">
              <div>{{this.getCount.related.keywords[0]}}</div>
            </el-collapse-item>
            <el-collapse-item title="网址" name="13">
              <a :href="this.getCount.information.url">{{this.getCount.information.url}}</a>
            </el-collapse-item>
          </el-collapse> -->
          <el-card :body-style="{ padding: '0px' }" v-if="JSON.parse(this.$route.query.item).newsUrl == '/information/findReportInfo'" >
            <img :src="this.getCount.information.img" class="image">
            <div style="padding: 14px;">
              <span><p>导读</p></span><span><p style="width:755px">{{this.getCount.information.guide}}</p></span>
            </div>
            <div style="padding: 14px;">
              <span>首次出版时间：</span><span>{{this.getCount.information.firstPostDate}}</span>
              <span class="Industry_Report_span_style">{{this.getCount.information.postDate}}</span><span class="Industry_Report_span0_style">最近修订时间：</span>
            </div>
            <div style="padding: 14px;">
              <span>交付方式：</span><span>{{this.getCount.information.delivery}}</span>
              <span class="Industry_Report_span_style">{{this.getCount.information.reportNum}}</span><span class="Industry_Report_span0_style">报告字数：</span>
            </div>
            <div style="padding: 14px;">
              <span>报告页数：</span><span>{{this.getCount.information.reportPage}}</span>
              <span  class="Industry_Report_span_style">{{this.getCount.information.production}}</span><span class="Industry_Report_span0_style">报告所属公司：</span>
            </div>
            <div style="padding: 14px;">
              <span>关键词：</span><span v-for="(li,key) in this.getCount.related.keywords" :key="key">   {{li}}</span>
              <span  class="Industry_Report_span_style">{{this.getCount.information.graphNum}}</span><span class="Industry_Report_span0_style">图表数量：</span>
            </div>
            <div style="padding: 14px;">
              <span>所属专业领域：</span><span>{{this.getCount.information.industry}}</span>
            </div>
            <div style="padding: 14px;">
              <span>网址：</span><span><a id="Industry_Report_a" :href="this.getCount.information.url">{{this.getCount.information.url}}</a></span>
            </div>
          </el-card>
        </div>
        <div class="box9-left">
          <x_related_reading></x_related_reading>
        </div>
        <div class="box10-left">
          <x_statement></x_statement>
        </div>
        <div class="box11-left">
          <x_comment></x_comment>
        </div>
      </div>
      <!--右边长列表-->
      <div class="box4-right">
        <div class="box5-right">
          <x_singlesidebar v-bind:tab_three1="tabthree1[10]" :isRequested="isRequested"></x_singlesidebar>
        </div>
        <div class="box6-right">
          <x_tab_switch v-bind:tabt1="tabthree1" v-bind:signal="signal[0].name" ></x_tab_switch>
        </div>
        <div class="box7-right">
          <x_tab_switch v-bind:tabt1="tabthree1" v-bind:signal="signal[1].name"></x_tab_switch>
        </div>
        <div class="box12-right">
          <x_singlesidebar v-bind:tab_three1="tabthree1[11]" :isRequested="isRequested"></x_singlesidebar>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";
import x_currentlocation from "./currentlocation.vue";
import x_singlesidebar from "./single_sidebar.vue";
import x_tab_switch from "./x_tab_switch.vue";
import x_hotsearch from "@/components/common/hotsearch.vue";
import Title from "@/components/news/Title.vue";
import x_Text from "@/components/news/Text.vue";
import x_related_reading from "@/components/news/related_reading.vue";
import x_statement from "@/components/news/x_statement.vue";
import x_comment from "@/components/news/comment.vue";

export default {
  name: "industry_dynamic_text",
  data() {
    return {
      last_location: "",
      last_last_location: "",
      current_place2: "正文",
      tabthree1: [
        {
          name: "用户中心",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "first", //子组件名
          message: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "订阅资讯",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "second", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "我的交易",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "third", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "近期活动",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "first", //子组件名
          message: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "协会活动",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "second", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "其他",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "third", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "行业动态",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "资讯热点",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "政策",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "行业报告",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        
        {
          name: "相关热点",
          belong: "相关热点",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "广告招商",
          belong: "广告招商",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        }
        
      ],
      isRequested:false,
      tabthree2: [
        {
          message: "近期活动"
        },
        {
          message: "协会活动"
        },
        {
          message: "其他"
        }
      ],
/*       tabsingle1: "相关热点",
      tabsingle2: "广告招商", */
      title: "",
      time: "",
      reading_amount: 0,
      content: "",
      source:"",
      signal:[
      {
          name:"用户"
      },
      {
          name:"活动"
      }
      ],
      getCount:{
        information:{

        },
        related:{

        }
      },
    };
  },
  methods: {
    init() {
      //接收跳转数据
      console.log(JSON.parse(this.$route.query.item));
      var data = this.$route.query.messageId;
      console.log(1);
      console.log(data)
      var item = JSON.parse(this.$route.query.item);
      if(item.list!=""){
        this.last_location = item.list + ">";
      }
      if(item.name!=""){
        this.last_last_location = item.name + ">";
      }
      this.last_last_location = item.name + ">";
      let newsUrl = item.newsUrl;
      var url = this.HOST + newsUrl;
        this.axios
          .get(url, {
            params: {
              id: data
            }
          })
          .then(response => {
            console.log(JSON.parse(this.$route.query.item));
            this.title = response.data.message.information.title;
            this.time = response.data.message.information.postDate;
            this.content = response.data.message.information.content[0].content;
            this.reading_amount = response.data.message.information.viewCount;
            
            this.getCount = response.data.message;
            this.isRequested = true;
          })
          .catch(function(response) {
            console.log(response);
          });
      this.$http.get(this.HOST +"/common/getIndex").then((response)=>{
          this.tabthree1[10].message =response.data.message.hotspot;  //给右边的相关赋值
        }).catch((response)=>{
          console.log(response.data);
        })
    }
  },
  components: {
    MHeader,
    x_currentlocation,
    x_singlesidebar,
    x_tab_switch,
    x_hotsearch,
    Title,
    x_Text,
    x_related_reading,
    x_statement,
    x_comment
  },
  mounted() {
    console.log(1);
    this.init();
  }, //组件被激活时调用
  activated() {
    console.log(1);
    this.init();
  } //组件被激活时调用
};
</script>

<style lang="scss" scoped>
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 280px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  width: 390px;
  height: 350px;
  float: right;
  margin-bottom:10px;
}
.hot_search {
  @include adv;
  height: 100%;
  //margin-left:15px;
  padding:13px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  width: 775px;
  float: left;
  //@include i-box;
  padding:13px;
  background: rgba(255, 255, 255, 0.95);
  div{
    width:775px;  //迫于无奈改的
  }
}
.box2-left {
  //@include i-box;
  //height: 50px;
  margin: 10px 0;
}
.out_line {
  @include adv;
  height: 100%;
}
/*左边文章样式*/
.box3-left {
  //@include i-box;
  height: 100%;
  margin: 10px 0;
 backgraound:rgba(255, 255, 255, 0.95);
}
.box4-right {
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div{
  margin-right:0.5px;
}
.box12-right,
.box5-right,
.box6-right,
.box7-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
.box8-left {
  //@include i-box;
  float: left;
  width: 795px;
  margin: 10px 0;
}
.box9-left {
  @include i-box;
  float: left;
  width: 795px;
  height: 200px;
  margin: 10px 0;
}
.box10-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 50px;
  margin: 10px 0;
}
.box11-left {
  //@include i-box;
  float: left;
  width: 795px;
  height: 400px;
  margin: 10px 0;
}
//行业报告的形式
.image{
  margin:0 auto;
  width:200px;
  margin-left:300px;
}
.Industry_Report_span_style{
  float:right;
  margin-right:200px;
}
.Industry_Report_span0_style{
float:right;
}
#Industry_Report_a{
  text-decoration: underline;
  color:#127de7;
}
#Industry_Report_a:hover{
  text-decoration: underline;
  color:red;
}
</style>


