<template>
  <div id="productIntroducePage">
    <div id="left" v-if="message">
      <div id="location">
        当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <router-link to="/product" class="selectable">产品</router-link> > <span class="blue">{{type}}</span>
      </div>
      <div class="leftOutBox">
          <div class="leftInBox">
            <h1 id="bigTitle">{{message.name}}</h1>
            <img id="productPic" v-bind:src=message.img>
            <div class=textBox>
                <span class="type">种类: </span>{{message.type}}<br>
                <span class="type">品牌: </span>{{message.brand||message.company||"null"}}<br>
                <span class="type">介绍: </span>{{message.introduction||"null"}}<br>
                <span class="type">产地: </span>{{message.residence||"unknown"}}<br>
                <span class="type">价格: </span>{{message.price||"unknown"}}<br>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "productIntroduce",
  data() {
    return {
        type:undefined,
        message:undefined,
    };
  },
  components: {
  },
  created:function(){
      this.type=this.$route.params.type;
      this.axios.get(this.HOST + '/product/getProductInfo',{
            params:{
                id:this.$route.params.id
            }
        }
        ).then((res)=>{
          this.message=res.data.message;
      })
  }
};
</script>

<style lang="scss">
div#productIntroducePage{
    width:1200px;
    margin:0 auto;
}
*{
    padding:0;
    margin:0;
}
.blue{
    color:#127de7;
}
.selected,.unselected{
    vertical-align:middle;
    border:1px solid black;
    outline:none;
    cursor:pointer;
    margin-top:3px;
    margin-right:3px;
    padding:1px 5px;
}
.selected{
    background:black;
    color:white;
}
.unselected{
    color:black;
}
div#location{
  margin-top:10px;
  width:700px;
  padding:5px;
  background:rgba(255,255,255,0.8);
}
div.leftOutBox{
    width:700px;                      
    padding:5px;
    background:rgba(255,255,255,0.8);
    margin-bottom:10px;
}
div.leftInBox{
    background:white;
    padding:5px;
}
h1#bigTitle{
    margin:1.5rem 0;
    text-align:center;
}
img#productPic{
    display:block;
    max-width:100%;
    margin:0 auto;
}
div#information{
    color:gray;
    position:relative;
    left:2rem;
}
div.textBox{
    padding:10px;
    background:white;
}
span.type{
    display:inline-block;
    margin-left:2rem;
    margin-top:4px;
    margin-bottom:4px;
    margin-right:0.5rem;
    font-weight:bold;
}
/*   以下是右边  */
/*              */
/*   ↖光↑↑翼↗   */
/*   ←( ·A·)→   */
/*   ↙展↓↓开↘   */
/*              */
</style>




