<template>
  <div id="listPage">
    <div id="left">
      <div id="location">
        当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <router-link to="/enterprise" class="selectable">企业</router-link> > <span class="blue">{{type + '列表'}}</span>
      </div>
      <div class="outBox">
        <div class="whiteBox">
          <ul v-if="message">
            <li v-for="(i,index) in message.list" class="newsLi selectable" @click.stop="routerLinkName('enterpriseNews',i.id,'id',type,'type')">{{i.title}}
              <div class="time">{{i.postDate}}</div>
            </li>
          </ul>
          <div class="selectBox" v-if="message">
            <button class="selectBox selectable" v-for="i in message.navigatepageNums" @click.stop="changePageNum(i)" v-bind:class='{chosen:i==pageNum}'>{{i}}</button>
          </div>
        </div>
      </div>
    </div>
    <enterpriseRight></enterpriseRight>
  </div>
</template>

<script>

import Vue from "Vue";
import enterpriseRight from "@/components/enterprise/enterpriseRight";

export default {
  name: "listPage",
  data(){
    return{
      message:undefined,
      params:undefined,
      type:'type',
      pageNum:undefined
    }
  },
  components: {
    enterpriseRight
  },
  created:function(){
    this.type=this.$route.params.type;
    this.pageNum=this.$route.params.pageNum;
    var newsUrl;
    if (this.type=="企业新闻") newsUrl='/information/getCompanyNewsList';
    if (this.type=="会员动态") newsUrl='/information/getCompanyDynamicsList';
    this.axios.get(this.HOST + newsUrl,{
      params:{
        pageNum:this.$route.params.pageNum
      }
    })
    .then((res)=>{
      this.message=res.data.message;
    })
  },
  methods:{
    changePageNum(i){
      this.pageNum=i;
    }
    ,
    routerLinkName:function(theName,paramValue,paramName,param2Value,param2Name){
            this.params=this.params || new Object();
            this.params[paramName]=paramValue;
            if (param2Name) {
                this.params[param2Name]=param2Value;
            }
            this.$router.push({name:theName,params:this.params});
        },
  },
  watch:{
    pageNum(){
      var newsUrl;
      if (this.type=="企业新闻") newsUrl='/information/getCompanyNewsList';
      if (this.type=="会员动态") newsUrl='/information/getCompanyDynamicsList';
      this.axios.get(this.HOST + newsUrl,{
        params:{
          pageNum:this.pageNum
        }
      })
      .then((res)=>{
        this.message=res.data.message;
      })
    }
  }
}
</script>
<style lang='scss'>
  div.outBox{
    background:rgba(255,255,255,0.8);
    padding:5px;
    padding-top:0;
    margin-bottom:10px;
  }
  div.whiteBox{
    background:white;
    padding:5px;
  }
  li.newsLi{
    width:585px;
    padding:5px;
    padding-right:100px;
    position:relative;
  }
  div.time{
    display:inline-block;
    position:absolute;
    right:5px;
    bottom:5px;
  }
  button{
    border:1px solid black;
    padding:2px 5px;
    margin:3px;
    outline:none;
  }
  .chosen{
    background:black;
    color:white;
  }
</style>




