            }
          ];
          break;//产品评测
        }
      }
      if(data.length == 1){
        switch(this.currentTab.list){
          case "资讯热点":
            data[1] = {
              img:
                "http://images.ofweek.com/Upload/News/2019-01/07/lime/1546825048553023275.png",
              title: "机器换人风口下，2019年哪个领域最值得投资？",
