              id: ""
            };  
            break;//协会动态
          case "企业新闻":
            data[1] = {
              img:
                "http://www.gdsjqr.com/attachment/cms/item/2018_05/16_17/250711c6397b4464.jpg",
              title: "安徽省委书记李锦斌率领安徽党政代表团莅临巨轮广州公司考察调研",
              id: ""
            }
            break;//企业新闻
          case "会员动态":
            data[1] = {
              img:
                "http://www.gdsjqr.com/attachment/cms/item/2018_09/12_08/efabfe0282717917.jpg",
              title: "活力智造 GSK系列工业机器人篇",
              id: ""
            };
            break;//会员动态
          case "产品新闻":
            data[1] = {
              img:
                "https://mp.ofweek.com/Upload/News/Img/member3802/201812/wx_article_20181205084132_hngzbL.jpg",
              title: "FANUC推出新机器人柔性伺服折弯系统 自动地板擦洗机器人正在进入沃尔玛",
              id: ""
            };
            break;//产品新闻
          case "产品评测":
            data[1] = {
              img:
                "http://images.ofweek.com/Upload/News/2015-4/YSQ/353.jpg",
              title: "去掉科技外衣 扫地机器人你选对了吗",
              id: ""
            };
            break;//产品评测
        }
      }
      return data;
    }
  },
  methods: {
    init() {
      if (this.myMessage) {  
        // console.log(this.myMessage);
        this.items = this.myMessage;
        this.items.forEach((item, index) => {
          this.items[index].postDate = item.postDate.slice(0, 10);
        });
        // console.log(this.items);
      }
    },
    routerLink(messageId, item) {  //判断到底是准备跳转之后发什么请求
      let data = JSON.stringify(item);
      if (this.myMessage) {
        // console.log(this.currentTab.list);

        let url = "/news/industry_dynamic_text";
        if (this.currentTab.name == "资讯") {
          url = "/news/industry_dynamic_text";
        }
        if (this.currentTab.name == "协会") {
          url = "/association/industry_dynamic_text";
        }
        if (this.currentTab.name == "产品") {
          url = "/product/industry_dynamic_text";
        }
        if (this.currentTab.name == "人才") {
          url = "/talent/talent_experience_text";
        }
        if (this.currentTab.list == "政策") {
          url = "/policy/policy_text";
        }
        if (this.currentTab.name == "企业") {
          url = "/enterprise/industry_dynamic_text";
        }
        this.$router.push({
          path: url,
          query: { messageId: messageId, item: data }
        });
      }
    }
  },
  watch: {
    isRequested() {
      this.init();
    }
  },
  activated() {
    this.init();
  },
  mounted() {
    this.init();
  }
};
</script>
<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#pb-list2 {
  @include whole;
  border-bottom: #ccc thin solid;
}
.pics {
  float: left;
  margin: 5px 25px;
}
.pics img {
  padding-left: 15px;
  width: 160px;
  height: 100px;
}
.pics p {
  margin: 0;
  text-align: center;
  max-width: 160px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 1px 2px 3px 15px;
  font-size: 0.8em;
  cursor: pointer;
}
.pics p:hover {
  color: #127de7;
}
#pb-list2 ul {
  list-style-type: none;
  padding: 0 15px;
  text-align: left;
  float: left;
  width: 540px;
  box-sizing: border-box;
}
#pb-list2 ul li {
  background: none;
  padding: 0;
  margin: 0;
  border-top: none;
  line-height: 15px;
  padding: 8px 0 7px;
  font-size: 13px;
  clear: both;
}
.list_title {
  color: #3f3f3f;
  display: inline-block;
  max-width: 350px;
  overflow: hidden;
  padding: 0 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 15px;
  vertical-align: middle;
  cursor: pointer;
}
.list_title:hover {
  color: #127de7;
}
#pb-list2 ul li b {
  float: right;
  padding: 5px 15px;
  font-weight: 100;
  display: block;
  height: 15px;
  overflow: hidden;
  font-family: Verdana;
  text-overflow: ellipsis;
}
.big {
  color: #df171e;
  padding: 4px 0;
  font-size: 16px;
  padding: 5px 10px;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



