<template>
  <div id="pen-list">
    <div class="boxLoading"  v-if="items[0].name == '张三丰'"></div>
    <div class="ul_list" v-if="items[0].name != '张三丰'">
      <div class="l_list">      
        <p v-for="(item,index) in items" v-if="index<8" class="perpon-name" @click="routerLink(index,currentTab)" :key="index">
        {{item.name}}
        </p>
      </div>
      <div class="r_list">      
        <p v-for="(item,index) in items" v-if="index>8 && index<16" class="perpon-name" @click="routerLink(index,currentTab)" :key="index">
        {{item.name}}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "pen-list",
  data() {
    return {
      items: [
        { name: "张三丰" },
        { name: "李四" },
        { name: "斯柯达" },
        { name: "阿尔法" },
        { name: "杰克逊" },
        { name: "史密斯" },
        { name: "阿达瓦" },
        { name: "阿凡达" },
        { name: "闪电侠" },
        { name: "小飞侠" },
        { name: "helloworld" },
        { name: "奥特曼" },
        { name: "abc" },
        { name: "bcs" },
        { name: "bcs" },
        { name: "abc" },
        { name: "bcs" },
        { name: "abc" }
      ],
      init_data: {
        name: "",
        belong: "",
        catalog: ""
      }
    };
  },
  props: ["myMessage", "isRequested", "currentTab"],
  watch: {
    isRequested() {
      this.init();
    }
  },
  methods: {
    init() {
      if (this.myMessage) {
        this.items = this.myMessage;
      }
    },
    routerLink(index, data) {
      let id = this.items[index].id;
      let address = "/talent/talent-detail";
      let item =JSON.stringify(data); 
      this.$router.push({ path: address, query: { id: id, data: item } });
    }
  },
  mounted() {
    this.init();
  },
  activated() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#pen-list {
  @include max;
}
.ul_list {
  width: 100%;
  float: right;
  box-sizing: border-box;
}
.l_list{
  width: 50%;
  float: left;
}
.r_list{
  width: 50%;
  float: left;
}
.perpon-name {
  display: inline-block;
  width: 160px;
  margin: 0 auto;
  padding: 4px 10px;
  text-align: center;
  color: #7b7b7b;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.perpon-name:hover {
  color: #2e2d2d;
}
/*loading css*/
.boxLoading {  
  width: 50px;
  height: 100px;
  margin: auto;
  position: relative;
  left: 0;
  right: 0;
  top: 65px;
  bottom: 0;
  &:before {
    content: '';
    width: 50px;
    height: 5px;
    background: #000;
    opacity: 0.1;
    position: absolute;
    top: 59px;
    left: 0;
    border-radius: 50%;
    animation: shadow .5s linear infinite;
  }
  &:after {
    content: '';
    width: 50px;
    height: 50px;
    background: #118dd7;
    animation: animate .5s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3px;
  }
}

@keyframes animate {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: translateY(18px) scale(1, .9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}
@keyframes shadow {
  0%, 100% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1);
  }
}
</style>



