<template>
  <div id="job-list">
    <div class="boxLoading" v-if="items[0].positionName == '数据加载中'"></div>
    <div  v-if="items[0].positionName != '数据加载中'">
      <ul>
        <li v-for="(item, index) in items" :key="index">
          <a class="list_title" @click="selectItem('/talent/talent-apply',item.id)"> {{item.positionName}} </a>
          <b class="pay" style="color: #gf971e;">{{item.salary}}</b>
          <b class="edu_record">{{item.degreeRequired}}</b>
          <b class="duration">{{item.workAge}}</b>
        </li>
      </ul>
      <div class="more" @click="selectItem('/talent/talent-job')" v-if="show">
        更多
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "job-lost",
  data() {
    return {
      items: [
        {
          if:0,
          positionName: "数据加载中", //工作职业
          salary: "5000-10000", //工资
          degreeRequired: "本科", //学历要求
          workAge: "1-3年" //工作时长
        }
      ],
      show: true
    };
  },
  methods: {
    selectItem(item,data) {
      if(data) this.$emit("select",item,data);
      else this.$emit("select", item);
    },
    init() {
      //检索路径判断“更多”是否显示
      if (this.$route.path.indexOf("job") != -1) {
        this.show = false;
      } else {
        this.show = true;
      }
      let that = this;
      let url = that.HOST + "/position/search";
      let params = {
        content: "",
        regionIds: [],
        industryId: ""
      };
      that
        .axios({
          method: "post",
          url: url,
          data: params
        })
        .then(response => {
          this.items = response.data.message.list.slice(0, 8);
          // this.data.list.forEach((item, index) => {
          //   this.items[index].createTime = item.createTime.slice(0, 10);
          // });
        })
        .catch(response => {
          that.$message.error({
            showClose: true,
            message: "获取招聘信息出现异常",
            duration: 2000
          });
        });
    }
  },
  created(){
    this.init();
  },
  // activated() {
  //   this.init();
  // },
  components: {}
};
</script>
<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#job-list {
  @include whole;
}
#job-list ul {
  & {
    list-style-type: none;
    padding: 0 15px;
    text-align: left;
  }
  li {
    & {
      padding: 4px 0 3px 10px;
      margin-left: 20px;
      font-size: 13px;
      clear: both;
      list-style: disc;
    }
    b {
      color: #5e5757;
      float: left;
      padding: 0 5px;
      font-weight: 100;
      display: block;
      height: 15px;
      overflow: hidden;
      font-family: Verdana;
      padding: 0;
      margin: 0;
      border-top: none;
      line-height: 15px;
      padding: 4px 0px 3px 20px;
      width: 150px;
    }
    .list_title {
      color: #7b7b7b;
      float: left;
      margin: 0;
      border-top: none;
      line-height: 15px;
      padding: 4px 0 3px;
      display: inline-block;
      width: 220px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      line-height: 15px;
      vertical-align: middle;
      cursor: pointer;
    }
    .list_title:hover {
      color: #127de7;
    }
  }
}
.more {
  cursor: pointer;
  text-align: center;
  box-sizing: border-box;
  margin: 15px auto;
  width: 150px;
  color: #409eff;
  padding: 4px 7px;
  // border: 1px solid #127de7;
  box-shadow: 0 0 1px #127de7;
  border-radius: 2px;
  text-decoration: none;
}
.more:hover {
  box-shadow: 0 0 1px #127de7;
}

/*loading css*/
.boxLoading {  
  width: 50px;
  height: 100px;
  margin: auto;
  position: relative;
  left: 0;
  right: 0;
  top: 80px;
  bottom: 0;
  &:before {
    content: '';
    width: 50px;
    height: 5px;
    background: #000;
    opacity: 0.1;
    position: absolute;
    top: 59px;
    left: 0;
    border-radius: 50%;
    animation: shadow .5s linear infinite;
  }
  &:after {
    content: '';
    width: 50px;
    height: 50px;
    background: #118dd7;
    animation: animate .5s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3px;
  }
}

@keyframes animate {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: translateY(18px) scale(1, .9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}
@keyframes shadow {
  0%, 100% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1);
  }
}
</style>



