<template>
  <div id="hot-list">
    <ul v-if="!blank">
      <li
        v-for="(item, index) in items"
        :key="index"
      >
        <a
          class="list_title"
          @click="routerLink(item.id,currentTab)"
        > {{item.title}} </a>
        <b>{{item.postDate}}</b>
      </li>
    </ul>
    <div id="banning" v-if="blank">
      <div id="tip">
        <div id="text">暂无数据</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "hot-list",
  props: ["currentTab", "isRequested"],
  data() {
    return {
      items: [],
      newsHotSpotDay:[],
      newsHotSpotMonth:[],
      newsHotSpotWeek:[],
      blank:false
    };
  },
  methods: {
    init() {
      let that = this;
      switch (that.currentTab.list) {
        case "今日":
          that.items = that.newsHotSpotDay;
          break;
        case "本周":
          that.items = that.newsHotSpotWeek;
          break;
        case "本月":
          that.items = that.newsHotSpotMonth;
          break;
      }
      if(that.items ==""){that.blank = true}
      else{that.blank = false}
    },
    routerLink(messageId, item) {  //判断到底是准备跳转之后发什么请求
      let data = JSON.stringify(item);
      if (this.items) {
        // console.log(this.currentTab.list);
        let url = "/news/industry_dynamic_text";
        this.$router.push({
          path: url,
          query: { messageId: messageId, item: data }
        });
      }
    }
  },
  watch: {
    isRequested() {
      this.init();
    }
  },
  activated(){
    this.init();
  },
  mounted(){
    let that = this;
    let url = that.HOST + "/information/getIndexNewsHotSpot";
    that.axios
      .get(url)
      .then(response => {
        if (response.data.result == "success") {
          that.newsHotSpotDay = response.data.message.newsHotSpotDay; 
          that.newsHotSpotMonth = response.data.message.newsHotSpotMonth; 
          that.newsHotSpotWeek = response.data.message.newsHotSpotWeek;
        } else if (response.data.result == "error") {
          that.$message.error({
            showClose: true,
            message: "主页数据异常",
            duration: 2000
          });
        }
      })
      .catch(function(response) {
        that.$message.error({
          showClose: true,
          message: "主页数据异常",
          duration: 2000
        });
      });
  }
};
</script>
<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#hot-list {
  @include whole;
}
#hot-list ul {
  & {
    list-style-type: none;
    padding: 0 15px;
    text-align: left;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
  li {
    & {
      padding: 6px 0 5px 10px;
      margin-left: 5px;
      font-size: 13px;
      clear: both;
      line-height: 20px;
    }
    .list_title {
      color: #3f3f3f;
      display: inline-block;
      max-width: 70%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      line-height: 15px;
      vertical-align: middle;
      cursor: pointer;
    } 
    .list_title:hover {
      color: #127de7;
    }
    b {
      float: right;
      padding: 0 5px;
      font-weight: 100;
      display: block;
      overflow: hidden;
      font-family: Verdana;
    }
  }
}
#banning {
  @include whole;
}
#text{
  font-size: 20px;
  color: rgb(138, 138, 138);
  text-align: center;
  line-height: 100%;
  margin: 100px;
}
</style>



