<template>
  <div id="x_singlesidebar">
    <div class="x_singlesidebar">
      <div class="x_singlesiderbar_title">
        <a>{{tab_three1.name}}</a>
      </div>
      <span class="x_singlesiderbar_title_more" @click="routerLink(tab_three1.name,-1)" v-if="tab_three1.name!='广告招商'&&tab_three1.name!='相关热点'&&tab_three1.name!='协会之窗'">
        <a>更多</a>
      </span>
    </div>
    <div class="x_singlesiderbar_content">
      <h3
        class="x_singlesiderbar_title_list_first"
        @click="routerLink(tab_three1.name,list.message[0].id)"
        v-if="tab_three1.name!='广告招商'&&list.message[0].title != '数据正在赶来啦。。。'"
      >
        <a v-if="tab_three1.name !='协会会员'">{{this.list.message[0].title}}</a>
        <a v-if="tab_three1.name =='协会会员'">{{this.list.message[0].name}}</a>
      </h3> <!--写得不适合很好-->
      <div class="boxLoading" v-if="list.message[0].title == '数据正在赶来啦。。。'&&tab_three1.name!='广告招商'&&tab_three1.name!='协会之窗'"></div>
      <li
        v-for="(li,index) of list.message"
        :key="index"
        class="x_singlesiderbar_li"
        v-if="tab_three1.name!='广告招商'&&index >= 1&& index <7&&list.message[0].title != '数据正在赶来啦。。。'&&tab_three1.name!='协会之窗'" 
        @click="routerLink(tab_three1.name,list.message[index].id)"
      >
        <a v-if="tab_three1.name !='协会会员'">{{li.title}}</a>
        <a v-if="tab_three1.name =='协会会员'">{{li.name}}</a>
        <a></a>
      </li>
      <associationIntroduce v-if="tab_three1.name=='协会之窗'"></associationIntroduce>
      <el-carousel trigger="click" height="150px" v-if="tab_three1.name=='广告招商'">
        <el-carousel-item v-for="(item,index) in imges" :key="index">
          <img :src="item.imgArray">
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>
<script>
import associationIntroduce from "@/components/common/association-introduce";
import PersonList from "@/components/common/person-list";

export default {
  components:{
    associationIntroduce,
    PersonList,
  },
  props: {
    tab_three1: {},
    isRequested: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    //保证数据再刷新的时候不会丢失
    isRequested() {
      if (this.tab_three1.message) {
        this.list = this.tab_three1;
      }
    },
    tab_three1:{  //监听相关热点，进行事后根据变化赋值
      handler(){
      this.list = this.tab_three1;
    },
    deep:true,
    }
  },
  data() {
    return {
      title: "广告招商",
      list_first: "世界机器人大会背后的中国制造",
      list: {
        name: "此处应有标题",
        message: [
          { title: "数据正在赶来啦。。。" },
          { title: "数据正在赶来啦。。。" },
          { title: "数据正在赶来啦。。。" },
          { title: "数据正在赶来啦。。。" },
          { title: "数据正在赶来啦。。。" },
          { title: "数据正在赶来啦。。。" },
          { title: "数据正在赶来啦。。。" },
          { title: "数据正在赶来啦。。。" }
        ]
      },
      imges: [
        {
          imgArray: "./static/PICS/advertise1.jpg"
        },
        {
          imgArray: "./static/PICS/advertise2.jpg"
        },
        {
          imgArray: "./static/PICS/advertise3.jpg"
        },
        {
          imgArray: "./static/PICS/advertise4.jpg"
        }
      ]
    };
  },
  methods: {
    init() {
      if (this.tab_three1.message) {
        //这样就算没有信息也可以显示原有信息
        this.list = this.tab_three1;
      }
    },
    routerLink(name, id) {
      let path;
      let url;  //用来传给正文用来发请求的
      let item = {
        newsUrl: "",
        list: "",
        name: "",
        requestPath: "",  //用来发给列表页面用来发请求的
        belong: ""
      };
      if (id != -1) {
        switch (name) {
          case "政策":
            url = "/information/findPolicyInfo";
            path = "/policy/industry_dynamic_text";
            item.name = "政策";
            item.requestPath = "/information/findPolicyList";
            break; //政策
          case "行业动态":
            url = "/information/findInformationInfo";
            path = "/news/industry_dynamic_text";
            item.name = "行业动态";
            item.requestPath = "/information/findInformationList";
            break; //行业动态
          case "行业报告":
            url = "/information/findReportInfo";
            path = "/news/industry_dynamic_text";
            item.name = "行业报告";
            item.requestPath = "/information/findReportList";
            break; //行业报告
          case "资讯热点":
            url = "/information/getHotspotInfo";
            path = "/news/industry_dynamic_text";
            item.name = "资讯热点";
            item.requestPath = "/information/findHotspotByPage";
            break; //资讯热点
          case "协会动态":
            url = "/information/getAssociationNewsInfo";
            path = "/news/industry_dynamic_text";
            item.name = "协会动态";
            item.requestPath = "/information/getAssociationNewsList";
            break; //协会动态
          case "通知公告":
            url = "/information/getNoticeInfo";
            path = "/news/industry_dynamic_text";
            item.name = "通知公告";
            item.requestPath = "/information/getNoticeList";
            break; //通知公告
          case "协会会员":  //会员动态列表
            url = "/introduction/getMemberInfo";
            path = "/news/industry_dynamic_text";
            item.name = "协会会员";
            item.requestPath = "/introduction/getMemberList";
            break; //协会会员
          case "协会之窗":  //好像是没用的写死的
            url = "/information/findReportInfo";
            path = "/news/industry_dynamic_text";
            item.name = "协会之窗";
            item.requestPath = "/information/findReportByPage";
            break; //协会之窗
          case "相关热点":
            url = "/information/getRelatedHotInfo";
            path = "/news/industry_dynamic_text";
            item.name = "相关热点";
            item.requestPath = "/information/getRelatedHot";
            break; //相关热点
          case "教育培训":
            url = "/information/getEducationTrainInfo";
            path = "/news/industry_dynamic_text";
            item.name = "教育培训";
            item.requestPath = "/information/getEducationTrainList";
            break; //教育培训
        }
      }
      if (id == -1) { //点击更多的时候
        switch (name) {
          case "政策":
            url = "/information/findPolicyInfo";
            path = "/policy";
            item.name = "政策";
            item.requestPath = "/information/findPolicyList";
            break; //政策
          case "行业动态":
            url = "/information/findInformationInfo";
            path = "/news/industry_dynamic";
            item.name = "行业动态";
            item.requestPath = "/information/findInformationList";
            break; //行业动态
          case "行业报告":
            url = "/information/findReportInfo";
            path = "/news/industry_dynamic";
            item.name = "行业报告";
            item.requestPath = "/information/findReportList";
            break; //行业报告
          case "资讯热点":
            url = "/information/getHotspotInfo";
            path = "/news/industry_dynamic";
            item.name = "资讯热点";
            item.requestPath = "/information/findHotspotByPage";
            break; //资讯热点
          case "协会动态":
            url = "/information/getAssociationNewsInfo";
            path = "/association/industry_dynamic";
            item.name = "协会动态";
            item.requestPath = "/information/getAssociationNewsList";
            break; //协会动态
          case "通知公告":
            url = "/information/getNoticeInfo";
            path = "/association/industry_dynamic";
            item.name = "通知公告";
            item.requestPath = "/information/getNoticeList";
            break; //通知公告
          case "协会会员":
            url = "/introduction/getMemberInfo";  //会员动态列表
            path = "/association/industry_dynamic";
            item.name = "协会会员";
            item.requestPath = "/introduction/getMemberList";
            break; //协会会员
          case "协会之窗":  //好像是没用的写死的
            url = "/information/findReportInfo";
            path = "/association/industry_dynamic";
            item.name = "协会之窗";
            item.requestPath = "/information/findReportByPage";
            break; //协会之窗
          case "相关热点":
            url = "/information/getRelatedHotInfo";
            path = "/news/industry_dynamic_text";
            item.name = "相关热点";
            item.requestPath = "/information/getRelatedHot";
            break; //相关热点
          case "教育培训":
            url = "/information/getEducationTrainInfo";
            path = "/news/industry_dynamic";
            item.name = "教育培训";
            item.requestPath = "/information/getEducationTrainList";
            break; //教育培训
        }
      }
      item.newsUrl = url;
      item = JSON.stringify(item);
      if(name == "协会会员"&&id == -1){
        let association_part = {
          name: "协会会员",
          belong: "协会", //归属的小标题
          path: "/association/vip-list", //点击跳转
          tocpn: "person-list", //子组件名
          message: null, //组件内容，对象数组
          isActive: false,
          requestPath: "/introduction/getMemberList", //会员列表url
          newsUrl: "/introduction/getMemberInfo" //具体会员请求url
        };
        item = JSON.stringify(association_part);
        this.$router.push({ path: association_part.path, query: { item } }); //路由跳转
      }else if(name == "协会会员"&&id != -1){
        let association_part = {
          name: "协会会员",
          belong: "协会", //归属的小标题
          path: "/association/vip-detail", //点击跳转
          tocpn: "person-list", //子组件名
          message: null, //组件内容，对象数组
          isActive: false,
          requestPath: "/introduction/getMemberList", //会员列表url
          newsUrl: "/introduction/getMemberInfo" //具体会员请求url
        };
        item = JSON.stringify(association_part);
        this.$router.push({ path: association_part.path, query: { item } }); //路由跳转
      }else{
        this.$router.push({
          path: path,
          query: {
            item: item,
            messageId: id
          }
      });
      }
    }
  },
  activated() {
    this.init();
  },
  /*       mounted() {
    this.init();
    }    */
};
</script>

<style lang="scss" scoped>
#x_singlesidebar {
  height: inherit;
}
* {
  font: 13px/22px "微软雅黑";
}
.x_singlesiderbar_title_list_first a {
  font-size: 16px;
  width: 500px;
  height: 20px;
  padding-left: 15px;
  display: block;
  margin-top: 15px;
  font-weight: bold;
  color: #3f3f3f;
  cursor: pointer;
  text-overflow: ellipsis;
  margin-bottom: 15px;
  overflow: hidden;
  white-space: nowrap;
}
.x_singlesidebar {
  color: #3f3f3f;
  background: #fff;
  height: 31px;
  display: block;
  width: 100%;
  padding: 5px 0px;
}
.x_singlesiderbar_title {
  float: left;
  line-height: 31px;
}
.x_singlesiderbar_title_more {
  display: inline-block;
  float: right;
}
.x_singlesiderbar_title_more a {
  font-size: 14px;
  float: right;
  height: 31px;
  line-height: 31px;
  display: inline-block;
  margin-right: 15px;
  padding-left: 20px;
}
.x_singlesiderbar_title_more :before {
  content: "+";
  display: inline-block;
  color: #127de7;
  text-align: center;
  vertical-align: top;
  position: static;
  font-size: 20px;
  width: 20px;
  height: 29px;
  line-height: 27px;
  /* background: url(../images/picxi.png) no-repeat -3px -586px; 图片旋转*/
  transition: all 0.2s;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -ms-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.x_singlesiderbar_title_more a {
  cursor: pointer;
}
.x_singlesiderbar_title a {
  color: #454545;
  text-align: center;
  height: 30px;
  line-height: 30px;
  display: block;
  padding: 0 15px;
  margin: 0 !important;
  background: #fff;
  color: #127de7 !important;
  font-weight: bold;
  font-size: 16px;
}
.x_singlesiderbar_content {
  min-height: 200px;
}
.x_singlesiderbar_li {
  font-weight: bold;
  width: auto;
  padding: 0 15px;
  height: 20px;
  font-size: 13px;
  padding: 15px;
  margin: 0 !important;
  border-top: 1px solid #dedede;
  padding: 3px 0;
  a {
    overflow: hidden;
    padding-left: 10px;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  a {
    padding-left: 15px;
  }
  &:hover {
    color: #e06a6a;
    cursor: pointer;
  }
}

/*loading css*/
.boxLoading {  
  width: 50px;
  height: 100px;
  margin: auto;
  position: relative;
  left: 0;
  right: 0;
  top: 80px;
  bottom: 0;
  &:before {
    content: '';
    width: 50px;
    height: 5px;
    background: #000;
    opacity: 0.1;
    position: absolute;
    top: 59px;
    left: 0;
    border-radius: 50%;
    animation: shadow .5s linear infinite;
  }
  &:after {
    content: '';
    width: 50px;
    height: 50px;
    background: #118dd7;
    animation: animate .5s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3px;
  }
}

@keyframes animate {
  17% {
    border-bottom-right-radius: 3px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: translateY(18px) scale(1, .9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}
@keyframes shadow {
  0%, 100% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1);
  }
}
body, html {
  background: #ccc;
  overflow: hidden;
  height: 100%;
}
</style>




